.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #fff;
  z-index: 99;
}
.container .navbar {
  position: relative;
  width: 100%;
  height: 44px;
  line-height: 44px;
  font-size: 18px;
  color: #fff;
  background: #108ee9;
  text-align: center;
  font-weight: bold;
}
.container .navbar .iconfont {
  position: absolute;
  font-size: 25px;
  left: 15px;
  font-weight: normal;
}
.container .loading {
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #333;
}
.container ul li a {
  display: block;
}
.container ul li a .singer-item {
  display: flex;
  align-items: center;
  color: #333;
  padding: 10px;
  border-bottom: 1px solid #eee;
}
.container ul li a .singer-item .avatar {
  margin-right: 10px;
}
.container ul li a .singer-item .avatar img {
  display: block;
  height: 60px;
  width: 60px;
  border-radius: 50%;
}
.container ul li a .singer-item .name {
  font-size: 15px;
  margin-bottom: 5px;
  color: #000;
}
